.backdrop {
  width: 100%;
  height: 100%;
  z-index: 100;
  transition-duration: var(--transition-duration);
  transition-timing-function: var(--transition-timing-function);
  background-color: rgba(18, 18, 18, .4);
  transition-property: visibility, opacity;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop--is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.scrollable {
  overflow-x: hidden;
  overflow-y: auto;
}

.backdrop__modal {
  opacity: 1;
  transition-duration: var(--transition-duration);
  transition-timing-function: var(--transition-timing-function);
  transition-property: transform, opacity;
}

.backdrop--is-hidden .backdrop__modal {
  opacity: 0;
  transform: translate(-50%);
}

